<template>
<div>
<img class="logo"  />
  <a-form-model
    ref="ruleForm"
    :model="form"
    :rules="rules"
    :label-col="labelCol"
    :wrapper-col="wrapperCol"
    class="container"
  >
    <a-form-model-item ref="username"  prop="username">
      <a-input   v-model="form.username" placeholder="请输入用户名"/>
    </a-form-model-item>
    <a-form-model-item ref="password"  prop="password">
      <a-input  type="password"  v-model="form.password"  placeholder="请输入密码"/>
    </a-form-model-item>
    <p v-if="errorhint" style="color:red;margin-left:100px">{{errorhint}}</p>
    <a-form-model-item :wrapper-col="{ span: 8, offset:3}">
      <a-button type="primary" @click="onSubmit">登录</a-button>
      <a-button style="margin-left: 10px;" @click="resetForm">重置</a-button>
    </a-form-model-item>
  </a-form-model>
</div>



</template>

<script>
import { loginUserPost } from "../api/api";
export default {
  data() {
    return {
      errorhint:'',
      labelCol: { span: 4},
      wrapperCol: { span: 12},

      form: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    async onSubmit() {
      // location.reload()
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          let params = {username:this.form.username,password:this.form.password}
          loginUserPost(params).then(res =>{
            if(!res) this.errorhint = "用户名或密码不正确"
            this.$store.commit('user_signin',res);    
            this.$router.push('/home')
          })
    
        } else {
          console.log("error submit!!");
         return false;
        }
      });
    },
    resetForm() {
      this.errorhint='';
      this.$refs.ruleForm.resetFields();
    }
  }
};
</script>

<style scoped>
.container{
  width: 600px;
  position:absolute;
  left: 70%;
  top:55%;
  transform: translate(-50%,-50%);
}
 .logo{
  background: url("../assets/loginbg.png");
  background-size: 100% 100%;
  height: 100%;
  position: fixed;
  width: 100%;
}  
.lable{
  color:white

}
</style>
